@import url(https://fonts.googleapis.com/css2?family=Exo+2:wght@100&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

body {
  height: 100vh;
  width: 100vw;
  background-image: url(/ytgrabber/static/media/pattern.f441d325.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-color: black;
  color: white;
  font-family: 'Exo 2', sans-serif; }

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start; }

.appTitle {
  padding: 0 25%;
  text-align: left;
  margin: 50px 0; }
  .appTitle a {
    text-decoration: none;
    color: white; }
  .appTitle .appName {
    color: white;
    text-decoration: none;
    font-size: 100px; }
  .appTitle .appDescription {
    font-size: 28px; }

.Form {
  margin-bottom: 100px;
  color: black;
  padding: 0 25%; }
  .Form .Input {
    padding: 15px 20px;
    font-size: 28px;
    color: white;
    width: 100%;
    font-family: inherit;
    text-align: center;
    text-overflow: ellipsis;
    background-color: black;
    border-radius: 0;
    border: solid 1px gray; }
    .Form .Input:focus {
      outline: none; }
  .Form .Button {
    font-size: 1.5em;
    padding: 10px 20px;
    border-radius: 5px;
    border: 0;
    background-color: black;
    color: white; }

.results-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 30px; }
  .results-container .qualHeader {
    background-color: black;
    color: white;
    padding: 5px 10px;
    text-decoration: underline; }
  .results-container .img-container {
    width: 300px;
    margin: 20px 30px;
    height: 175px;
    object-fit: cover; }

@media (max-width: 1024px) {
  .appTitle {
    padding: 0 15%;
    margin: 50px 0; }
  .Form {
    margin-top: 20px;
    margin-bottom: 30px;
    padding: 10px 0;
    max-width: 100%; }
    .Form .Input {
      max-width: 100%;
      width: auto; }
  .results-container {
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 50px 0 0; } }

